body {
  text-align: center;
  background-color: #f4f4f9;
  margin: 0;
  padding: 20px;
}

.gallery {
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 15px;
  margin-top: 20px;
  display: grid;
}

.gallery-item {
  border-radius: 10px;
  transition: all .2s;
  overflow: hidden;
  box-shadow: 0 4px 10px #0000001a;
}

.gallery-item:hover {
  transform: scale(1.05);
}

.gallery-item img {
  object-fit: cover;
  width: 100%;
  height: 150px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #06f;
  border: none;
  border-radius: 5px;
  margin-top: 20px;
  padding: 10px 20px;
  transition: all .2s;
}

button:hover {
  background-color: #04c;
}
